<template>
  <div class="user">
    <img class="mei-img" :src="'http://meilapp.qiniucdn.com'+user.avatar" alt="">
    <div class="mei-official">{{user.nickname}} <span><i>L{{user.level}}</i></span></div>
    <div class="fans">{{user.fans_count}} 粉丝</div>
    <div class="no-notice" v-if="!isFollow">关注ta</div>
    <div class="notice" v-if="isFollow">已关注</div>
  </div>
</template>

<script>
export default {
  props: {
    user: {
      type: Object,
      default () {
        return {}
      }
    },
    isFollow: {
      type: Boolean,
      default () {
        return false
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .user {
    width: 100%;
    padding: 20px 10px;
    border-top: 10px solid #DDDDDD;
    border-bottom: 10px solid #DDDDDD;
    text-align: center;
    box-sizing: border-box;
    .mei-img {
      width: 60px;
      border-radius: 30px;
    }
    .mei-official {
      line-height: 30px;
      font-size: 14px;
      letter-spacing: 1px;
      font-family: Microsoft YaHei;
      span {
        color: white;
        background-color: black;
      }
    }
    .fans {
      font-size: 13px;
      line-height: 30px;
    }
    .no-notice, .notice {
      width: 120px;
      height: 25px;
      color: pink;
      border: 1px solid pink;
      border-radius: 20px;
      margin: 0 auto;
    }
  }
</style>



