<template>
  <div class="hairdressing-home">
    <header-bar title="最热话题">
      <div slot="left-actions" @click="toBack()">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
    </header-bar>
    <div class="view">
      <makeup-header title="秀美发" count='21212' :leftImg="leftImg" :rightImg="rightImg"></makeup-header>
      <div class="tabs">
        <div class="tab hairdresser">
          <div class="chinese">{{chinese}}</div>
          <div class="eglish">{{eglish}}</div>
          <img class="photo" :src="leftImg">
        </div>
        <div class="tab course">
          <div class="chinese">DIY教程</div>
          <div class="eglish">course</div>
          <img class="photo" :src="leftImg">
        </div>
        <div class="tab choice">
          <div class="chinese">款式选择</div>
          <div class="eglish">choice</div>
          <img class="photo" :src="leftImg">
        </div>
      </div>
      <nail-list :leftNails="leftNails" :rightNails="rightNails" v-if="nails.length > 0"></nail-list>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../../components/HeaderBar.vue'
import MakeupHeader from '../../components/MakeupHeader.vue'
import NailList from '../NailList.vue'
import { getMakeupItemData } from '../../apis/request/getData.js'
import leftImg from '../../static/img/home_normal.png'
import rightImg from '../../static/img/home_selected.png'

export default {
  data () {
    return {
      leftImg: leftImg,
      rightImg: rightImg,
      nails: [],
      leftNails: [],
      rightNails: [],
      chinese: '',
      eglish: ''
    }
  },
  components: {
    HeaderBar,
    MakeupHeader,
    NailList
  },
  computed: {
    tab () {
      return this.$route.query.index
    }
  },
  async mounted () {
    if (this.tab === 1) {
      this.chinese = '美发师'
      this.eglish = 'Hairdresser'
    }
    if (this.tab === 2) {
      this.chinese = '美甲师'
      this.eglish = 'Mainicurist'
    }
    const result = await getMakeupItemData(this.tab)
    this.nails = result.data[0].nails
    this.leftNails = this.nails.filter((v, index) => {
      return index % 2 === 1
    })
    this.rightNails = this.nails.filter((v, index) => {
      return index % 2 === 0
    })
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .hairdressing-home {
    width: 100%;
    height: 100%;
    .view {
      position: absolute;
      top: 64px;
      width: 100%;
      height: calc(100% - 64px);
      overflow: auto;
      .tabs {
        display: flex;
        flex-flow: row nowrap;
        width: 100%;
        .tab {
          position: relative;
          padding: 5px;
          width: 32%;
          height: 85px;
          margin-left: 1%;
          box-sizing: border-box;
          border-radius: 5px;
          .photo {
            position: absolute;
            right: 5px;
            bottom: 5px;
          }
          .eglish {
            font-size: 10px;
          }
        }
        .hairdresser {
          background-color: #FFFFCC;
          color: #CCFFCC;
        }
        .course {
          background-color: #CCFFCC;
          color: #CCCC66;
        }
        .choice {
          background-color: #CCCCCC;
          color: #CC99FF;
        }
      }
    }
  }
</style>



