<template>
  <div>
    <div class="beauty" v-for="(feed, index) in items" :key="index" @click="toDetail(feed)">
      <div v-if="feed.feed && feed.feed.title" class="today">{{feed.feed.title}}</div>
      <div v-if="feed.extra&&extraTitle && feed.extra.top" class="today">{{extraTitle}}</div>
      <div class="user" v-if="feed.user">
        <img class="avatar" :src="'http://meilapp.qiniucdn.com'+feed.user.avatar" alt="">
        <span class="nickname">{{feed.user.nickname}}</span>
        <span class="level">L{{feed.user.level}}</span>
        <div class="notice">关注ta</div>
      </div>
      <div class="user" v-if="feed.actor">
        <img class="avatar" :src="'http://meilapp.qiniucdn.com'+feed.actor.user.avatar" alt="">
        <span class="nickname">{{feed.actor.user.nickname}}</span>
        <span class="level">L{{feed.actor.user.level}}</span>
        <div class="notice">关注ta</div>
      </div>
      <div v-if="feed.img" class="img-play">
        <img width="100%" :src="'http://meilapp.qiniucdn.com'+feed.img.img4" alt="">
        <i class="fa fa-play fa-3x play-button" aria-hidden="true" v-if="play"></i>
      </div>
      <div v-if="feed.feed">
        <div v-if="feed.feed.img_v4" class="img-play">
          <img width="100%" :src="'http://meilapp.qiniucdn.com'+feed.feed.img_v4.img" alt="">
          <i class="fa fa-play fa-3x play-button" aria-hidden="true" v-if="play"></i>
        </div>
        <pre class="summary" v-if="feed.feed.summary && !feed.feed.title">{{feed.feed.summary}}</pre>
        <tags :items="feed.feed.vtalk_tags" v-if="feed.feed.vtalk_tags"></tags>
        <div class="comment-like" v-if="!feed.feed.title">
          <span class="comment" v-if="feed.participate_count"><i class="fa fa-comment-o fa-lg"></i> {{feed.participate_count}}</span>
          <span class="like" v-if="feed.like_count"><i class="fa fa-heart-o fa-lg"></i> {{feed.like_count}}</span>
        </div>
      </div>
      <div v-if="feed.like_info" class="videos-desc">
        <div class="title" v-if="feed.title">{{feed.title}}</div>
        <div class="count">
          <div class="played">played {{feed.played_count}}</div>
          <div class="time"><i class="fa fa-clock-o" aria-hidden="true"></i> {{feed.runtime}}</div>
          <div class="like">
            <i class="fa fa-heart-o fa-lg"></i>
            <div class="count">{{feed.like_info.like_count}}</div>
          </div>
        </div>
      </div>
      <div class="icon-play-circle"></div>
    </div>
  </div>
</template>

<script>
import Tags from '../components/Tags.vue'

export default {
  props: {
    items: {
      type: Array,
      default () {
        return []
      }
    },
    play: {
      type: Boolean,
      default () {
        return false
      }
    }
  },
  components: {
    Tags
  },
  computed: {
    extraTitle () {
      console.info(this.items[1], 'mish item1')
      if (this.items[0].extra) {
        return this.items[0].extra.top.title
      }
      return ''
    }
  },
  methods: {
    toDetail (item) {
      let slug
      let path
      if (!item.feed || !item.feed.jump_data) {
        slug = item.objid
        path = '/beauty-videos-detail'
      } else {
        slug = item.feed.jump_data
        path = '/beauty-talk'
      }
      this.$router.push({
        path: path,
        query: {
          id: slug
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .beauty {
    position: relative;
    width: 100%;
  }
  .fa-lg {
    vertical-align: 0%;
    color: gray;
  }
  .play-button {
    position: absolute;
    top: 50%;
    left: 50%;
    color: white;
    transform: translate(-50%, -50%);
  }
  .today {
    position: relative;
    height: 30px;
    width: 100%;
    line-height: 20px;
    text-align: center;
    background-color: #EEEEEE;
    font-size: 10px;
  }
  .user {
    position: relative;
    height: 44px;
    width: 100%;
    line-height: 44px;
    box-sizing: border-box;
    padding-left: 45px;
    .avatar {
      position: absolute;
      width: 30px;
      border-radius: 15px;
      top: 7px;
      left: 10px;
    }
    .level {
      color: white;
      background-color: black;
    }
    .notice {
      position: absolute;
      top: 12px;
      width: 60px;
      right: 10px;
      border-radius: 20px;
      text-align: center;
      height: 20px;
      line-height: 20px;
      border: 1px solid #CCCCCC;
      font-size: 10px;
      color: black;
    }
  }
  .summary {
    text-overflow: ellipsis;
    white-space: pre-wrap;
    padding: 0 10px;
    box-sizing: border-box;
    line-height: 20px;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    letter-spacing: 1px;
  }
  .img-play {
    position: relative;
  }
  .comment-like {
    position: relative;
    font-size: 12px;
    margin-top: 10px;
    height: 30px;
    border-bottom: 10px solid #EEEEEE;
    .comment {
      position: absolute;
      right: 90px;
      .fa-lg {
        font-size: 1.4em;
      }
    }
    .like {
      position: absolute;
      right: 10px;
    }
  }
  .videos-desc {
    position: absolute;
    bottom: 0;
    width: 100%;
    color: white;
    height: 50px;
    letter-spacing: 1px;
    background-color: rgba(153, 153, 153, 0.2);
    font-family: PMingLiU;
    .title {
      font-size: 13px;
      position: absolute;
      bottom: 30px;
      left: 15px;
    }
    .count {
      font-size: 10px;
      .played {
        position: absolute;
        left: 10px;
        bottom: 10px;
      }
      .time {
        position: absolute;
        left: 100px;
        bottom: 10px;
      }
      .like {
        position: absolute;
        bottom: 10px;
        right: 10px;
        display: flex;
        flex-flow: column;
        align-items: center;
        .fa-lg {
          color: white;
          padding-bottom: 8px;
          font-size: 1.6em;
        }
      }
      .like-count {
        position: absolute;
        bottom: 10px;
        right: 10px;
      }
    }
  }
</style>



