<template>
  <div class="beauty-daquan">
    <header-bar :title="title">
      <div slot="left-actions" class="left-actions" @click="toBack">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
    </header-bar>
    <div class="view">
      <div class="recommend-buttons">
        <p class="title" v-if="recommendButtons.title">- {{recommendButtons.title}} -</p>
        <div class="buttons">
          <div class="button" v-if="buttons.length > 0" v-for="(button, index) in buttons" :key="index">{{button.title}}</div>
        </div>
      </div>
      <div class="all-buttons" v-if="allButtons.length > 0" v-for="(allButton, index) in allButtons" :key="index">
        <p class="title">- {{allButton.title}} -</p>
        <div class="buttons-img">
          <img class="button-img" v-for="buttonImg in allButtons[index].buttons" :src="'http://meilapp.qiniucdn.com'+buttonImg.img" alt=""  :key="buttonImg.jump_data">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'
import { getMakeupItemData } from '../apis/request/getData.js'

export default {
  data () {
    return {
      title: '妆容大全',
      data: {}
    }
  },
  components: {
    HeaderBar
  },
  computed: {
    recommendButtons () {
      if (JSON.stringify(this.data) !== '{}') {
        return this.data.recommend_buttons
      }
      return {}
    },
    buttons () {
      if (JSON.stringify(this.recommendButtons) !== '{}') {
        return this.recommendButtons.buttons
      }
      return []
    },
    allButtons () {
      if (JSON.stringify(this.data) !== '{}') {
        return this.data.all_buttons
      }
      return []
    }
  },
  async mounted () {
    const result = await getMakeupItemData(0)
    this.data = result.data[0]
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .beauty-daquan {
    width: 100%;
    height: 100%;
    .view {
      position: absolute;
      top: 64px;
      width: 100%;
      height: calc(100% - 64px);
      overflow: auto;
    }
    .left-actions {
      font-size: 24px;
      font-weight: 200;
      color: gray;
    }
    .recommend-buttons {
      text-align: center;
      border-bottom: 10px solid #DDDDDD;
      padding-bottom: 10px;
      .title {
        font-size: 12px;
      }
      .buttons {
        display: flex;
        flex-flow: row wrap;
        justify-content: center;
        font-size: 12px;
        .button {
          width: 20%;
          height: 30px;
          line-height: 30px;
          border: 1px solid #DDDDDD;
          margin: 10px 10px 0 0;
          border-radius: 20px;
        }
      }
    }
    .all-buttons {
      text-align: center;
      font-size: 12px;
      .buttons-img {
        width: 100%;
        display: flex;
        flex-flow: row wrap;
        .button-img {
          width: 30%;
          height: 30%;
          margin-left: 2.5%;
          padding-bottom: 2.5%;
        }
      }
    }
  }
</style>



