<template>
  <div class="makeup-topic">
    <header-bar :title="title">
      <div slot="left-actions" @click="toBack()">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
    </header-bar>
    <div class="view">
      <makeup-list :items="makeupItems"></makeup-list>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'
import MakeupList from './MakeupList.vue'
import { makeupTopicData } from '../apis/request/getData.js'

export default {
  data () {
    return {
      title: '美妆专题',
      makeupItems: []
    }
  },
  components: {
    HeaderBar,
    MakeupList
  },
  async mounted () {
    const result = await makeupTopicData()
    this.makeupItems = result.data
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .makeup-topic {
    width: 100%;
    height: 100%;
    .view {
      position: absolute;
      padding-top: 10px;
      top: 64px;
      width: 100%;
      height: calc(100% - 64px);
      overflow: auto;
      border-top: 0.5px solid #EEE;
    }
  }
</style>



