<template>
  <div class="vtalk-tags" v-if="items.length > 0" :class="{'tags-border': border}">
    <span v-for="(tag, index) in items" :key="index">{{tag.title}}</span>
  </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array,
      default () {
        return []
      }
    },
    border: {
      type: Boolean,
      default () {
        return false
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .tags-border {
    border-top: 1px solid #ddd;
  }
  .vtalk-tags {
    font-size: 10px;
    color: #FF80AB;
    padding-left: 5px;
    box-sizing: border-box;
    padding: 10px 10px 10px 0;
    span {
      margin-left: 10px;
    }
  }
</style>



