<template>
  <div class="beauty-videos">
    <header-bar :title="title">
      <div slot="left-actions" @click="toBack()">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
    </header-bar>
    <div class="tabs">
      <div class="tab" v-for="tab in classifyList" :key="tab.tag" :class="{ selected: tab.tag === currentTag }">
        {{tab.title}}
      </div>
    </div>
    <div class="beauty-list" v-if="videos.length > 0">
      <div class="top-border"></div>
      <beauty-list :items="videos" :play="true"></beauty-list>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'
import BeautyList from './BeautyList.vue'
import { getAllVideos } from '../apis/request/getData.js'

export default {
  data () {
    return {
      title: '美妆视频',
      videos: [],
      allData: {},
      classifyList: [],
      currentTag: 0
    }
  },
  components: {
    HeaderBar,
    BeautyList
  },
  async mounted () {
    const allResult = await getAllVideos()
    this.classifyList = allResult.data[0].classify_list
    this.videos = allResult.data[0].videos
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .beauty-videos {
    width: 100%;
    height: 100%;
    .tabs {
      position: absolute;
      top: 64px;
      width: 100%;
      height: 80px;
      display: flex;
      flex-flow: row wrap;
      align-items: center;
      text-align: center;
      font-size: 12px;
      box-sizing: border-box;
      .selected {
        color: pink;
      }
      .tab {
        width: 25%;
      }
    }
    .beauty-list {
      position: absolute;
      top: 144px;
      width: 100%;
      height: calc(100% - 144px);
      overflow: auto;
      .top-border {
        width: 100%;
        height: 10px;
        background-color: #EEE;
      }
    }
  }
</style>



