<template>
  <div class="beauty-home">
    <div class="header">
      <i class="fa fa-search search"></i>
      <span class="tab" @click="toGoodsList()" :class="{ selected: current === 0 }">晒好货</span>
      <span class="tab" @click="toMakeUp()" :class="{ selected: current === 1 }">学化妆</span>
      <span class="tab" @click="toFollow()" :class="{ selected: current === 2 }">关注</span>
      <!-- <img class="settings" src="../assets/icons/settings.svg" alt=""> -->
    </div>
    <div class="view">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      current: 0
    }
  },
  mounted () {
    // this.$router.push('/beauty-home/goods-bask')
    console.info('goods-bask mounted')
  },
  methods: {
    toMakeUp () {
      console.log('/beauty-home/make-up mish')
      this.$router.push('/beauty-home/make-up')
      this.current = 1
    },
    toGoodsList () {
      this.$router.push('/beauty-home/goods-bask')
      this.current = 0
    },
    toFollow () {
      this.$router.push('/beauty-home/follow')
      this.current = 2
    }
  }
}
</script>

<style scoped lang="scss">
  .beauty-home {
    width: 100%;
    .selected {
      color: #FF80AB;
      border-bottom: 2px solid #FF80AB;
    }
    .header {
      position: absolute;
      top: 20px;
      padding-top: 10px;
      box-sizing: border-box;
      width: 100%;
      height: 44px;
      border-bottom: 0.5px solid #DDDDDD;
      text-align: center;
      .tab {
        display: inline-block;
        height: 92%;
        margin-right: 20px;
      }
      .settings, .search {
        position: absolute;
        width: 30px;
        color: gray;
      }
      .settings {
        right: 15px;
      }
      .search {
        left: 15px;
        font-size: 1.4em;
        color: #BDBDBD;
      }
    }
    .view {
      position: absolute;
      top: 64px;
      width: 100%;
      height: calc(100% - 108px);
      overflow: auto;
    }
    .bottom-tabs {
      position: absolute;
      width: 100%;
      height: 44px;
      bottom: 0;
      box-sizing: border-box;
      padding-top: 5px;
      background-color: #EEEEEE;
    }
  }
</style>



