<template>
  <div class="beauty-talk">
    <header-bar>
      <div slot="left-actions" @click="toBack()">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
      <img class="header-img" :src="'http://meilapp.qiniucdn.com'+data.user.avatar" alt="" v-if="hasData">
    </header-bar>
    <div class="talks">
      <img width="100%" :src="'http://meilapp.qiniucdn.com'+img" alt="">
      <div class="text">
        <pre class="title">{{data.title}}</pre>
        <div class="summary">{{data.summary}}</div>
      </div>
      <div class="list1" v-for="(item, index) in list1" :key="index">
        <img width="100%" :src="'http://meilapp.qiniucdn.com'+item.imgs[0].img" alt="" v-if="item.imgs.length > 0">
        <div class="content" v-if="item.content">{{item.content}}</div>
      </div>
      <div class="talk">
        <p class="title">大家在讨论</p>
        <product-list :items="data.used_products" v-if="hasData" :border="false"></product-list>
        <tags :items="data.vtalk_tags" v-if="hasData" :border="true"></tags>
      </div>
      <user :user="data.user" :isFollow="data.is_follow" v-if="hasData"></user>
      <div class="comments-top">
        <div class="time"><i class="fa fa-clock-o" aria-hidden="true"></i> 3个人月前</div>
        <div class="eye"><i class="fa fa-eye fa-lg"></i> {{visitCount}}</div>
        <div class="comment-count"><i class="fa fa-comment-o fa-lg"></i> {{data.comment_count}}</div>
        <div class="like"><i class="fa fa-heart-o fa-lg"></i> {{likeCount}}</div>
      </div>
      <comment-list
        v-if="hasData"
        :items="list2"
        :isShow="data.comment_count > 5"
        @to-detail="viewComments($event)">
      </comment-list>
      <div class="maybe-intresting">
        <div class="title">- 你可能感兴趣 -</div>
        <makeup-list :items="vtalks" v-if="vtalks.length > 0"></makeup-list>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'
import CommentList from '../components/CommentList.vue'
import Star from '../components/Star.vue'
import Tags from '../components/Tags.vue'
import User from './User.vue'
import MakeupList from './MakeupList.vue'
import ProductList from '../rankingList/ProductList.vue'
import { getBeautyTalk } from '../apis/request/getData.js'

export default {
  data () {
    return {
      data: {},
      img: '',
      vtalks: {},
      list1: [],
      list2: [],
      hasData: false
    }
  },
  components: {
    HeaderBar,
    Star,
    MakeupList,
    CommentList,
    User,
    ProductList,
    Tags
  },
  computed: {
    id () {
      return this.$route.query.id
    },
    likeCount () {
      if (this.hasData) {
        return this.data.like_info.like_count
      }
      return 0
    },
    visitCount () {
      if (this.hasData) {
        return this.data.visit_count
      }
      return 0
    }
  },
  async mounted () {
    const result = await getBeautyTalk(this.id)
    this.data = result.data[0]
    console.info(this.data.comment_count, 'mish comment_count')
    this.hasData = JSON.stringify(this.data) !== '{}'
    this.img = this.data.imgs[0].img
    this.vtalks = this.data.vtalks
    this.list1 = this.data.list1
    this.list2 = this.data.list2
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    },
    viewComments () {
      this.$router.push(`/beauty-talk-comments/${this.id}`)
    }
  }
}
</script>

<style scoped lang="scss">
  .beauty-talk {
    width: 100%;
    .header-img {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 35px;
      border-radius: 20px;
    }
    .talks {
      width: 100%;
      position: absolute;
      top: 64px;
      height: calc(100% - 108px);
      overflow: auto;
      .text {
        font-size: 12px;
        width: 100%;
        padding: 10px 10px 0 10px;
        box-sizing: border-box;
        .title {
          white-space: pre-wrap;
          font-family: MingLiU;
          line-height: 20px;
          margin: 0;
          letter-spacing: 1px;
          color: #616161;
        }
      }
      .talk {
        .title {
          border-bottom: 0.5px solid #ddd;
          margin: 0;
          padding: 15px;
          font-size: 14px;
          font-family: Monospace;
        }
      }
      .list1 {
        img {
          margin-top: 20px;
        }
        .content {
          font-size: 12px;
          white-space: pre-wrap;
          padding: 10px 10px 0 10px;
        }
      }
      .comments-top {
        display: flex;
        padding: 0 10px;
        // justify-content: center;
        height: 40px;
        line-height: 40px;
        border-bottom: 0.5px solid #ddd;
        font-size: 12px;
        color: gray;
        .time {
          padding-right: 10px;
        }
        .eye {
          padding-left: 20px;
        }
        .comment-count {
          padding-left: 90px;
        }
        .like {
          padding-left: 20px;
        }
      }
      .maybe-intresting {
        .title {
          height: 50px;
          line-height: 50px;
          text-align: center;
        }
      }
    }
  }
</style>



