<template>
  <div>
    <swiper :options="swiperOption" id="swiper">
      <swiper-slide class="swiper-slide-makeup" v-for="(item, index) in makeupButtons" :key="index">
        <img class="makeup-img" :src="'http://meilapp.qiniucdn.com'+item.img" alt="图片" width="100" onerror="onerror=null;src='http://iph.href.lu/100x100'" @click="toMakeup(index)">
      </swiper-slide>
    </swiper>
    <div class="beauty-makeups">
      <div class="beauty-makeup" v-for="(item, index) in beautyMakeups" :key="index" @click="toTap(index)">
        <img class="beauty-img" :src="'http://meilapp.qiniucdn.com'+item.img" alt="" :key="index" width="38px">
        <span class="title">{{item.title}}</span>
      </div>
    </div>
    <div class="user-topic-list">
      <beauty-list :items="videos" :play="true"></beauty-list>
      <div class="videos-makeup">
        <img class="makeup-img"
          :src="'http://meilapp.qiniucdn.com'+item.img" alt="图片" width="60" v-for="(item, index) in lastVideosButtom.imgs" :key="index">
        <div class="titles" @click="toBeautyVideos()">
          <div class="title">{{lastVideosButtom.title}} ></div>
          <div class="sub-title">{{lastVideosButtom.sub_title}}</div>
        </div>
      </div>
      <beauty-list :items="feeds" :play="true"></beauty-list>
    </div>
  </div>
</template>

<script>
import { getMakeUpData } from '../apis/request/getData.js'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import BeautyList from './BeautyList.vue'

export default {
  data () {
    return {
      data: {},
      swiperOption: {
        width: 80,
        notNextTick: true,
        direction: 'horizontal',
        setWrapperSize: true,
        autoHeight: true,
        observeParents: true,
        freeMode: true
      },
      lastVideosButtom: {}
    }
  },
  components: {
    swiper,
    swiperSlide,
    BeautyList
  },
  computed: {
    makeupButtons () {
      return this.data.makeup_buttons
    },
    beautyMakeups () {
      return this.data.beauty_makeups
    },
    feeds () {
      return this.data.feeds
    },
    videos () {
      return this.data.videos
    }
    // lastVideosButtom () {
    //   return this.data.videos.pop().extra.bottom
    // }
  },
  async mounted () {
    const result = await getMakeUpData()
    this.data = result.data[0]
    this.lastVideosButtom = this.data.videos.pop().extra.bottom
  },
  methods: {
    toMakeup (index) {
      console.info(index, 'mish index')
      if (index === 0 || this.makeupButtons.length - 1) {
        this.$router.push('/beauty-daquan')
      }
    },
    toTap (index) {
      if (index === 0) {
        this.$router.push('/beauty-videos')
      }
      if (index === 1) {
        this.$router.push({
          path: '/dressing-makeup-home',
          query: {
            index: 1
          }
        })
      }
      if (index === 2) {
        this.$router.push({
          path: '/dressing-makeup-home',
          query: {
            index: 2
          }
        })
      }
      if (index === 3) {
        this.$router.push({
          path: '/face-makeup-home',
          query: {
            index: 3
          }
        })
      }
      if (index === 4) {
        this.$router.push({
          path: '/face-makeup-home',
          query: {
            index: 4
          }
        })
      }
      if (index === 5) {
        this.$router.push({
          path: '/face-makeup-home',
          query: {
            index: 5
          }
        })
      }
    },
    toBeautyVideos () {
      this.$router.push('/beauty-videos')
    }
  }
}
</script>

<style>
  .swiper-wrapper {
    margin-left: 18px !important;
  }
  .swiper-slide-makeup {
    width: 120px !important;
  }
</style>

<style scoped lang="scss">
  #swiper{
    width: 100%;
    height: 100px;
    padding-top: 12px;
    padding-bottom: 12px;
    background-color: white;
    border-bottom: 15px solid #EEEEEE;
  }
  .beauty-makeups {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    text-align: center;
    margin: 10px 0;
    .beauty-makeup {
      width: 33%;
      .beauty-img {
        vertical-align: middle;
      }
      .title {
        display: inline-block;
        font-size: 12px;
      }
    }
  }
  .titles {
    height: 100px;
    box-sizing: border-box;
    padding-top: 10px;
    padding-left: 20px;
    .title {
      font-size: 14px;
      color: pink;
    }
    .sub-title {
      font-size: 9px;
    }
  }
  .videos-makeup {
    display: flex;
    flex-flow: row nowrap;
    background-color: white;
    height: 60px;
    margin: 10px 0;
    .makeup-img {
      margin-left: 10px;
    }
  }
</style>



