<template>
  <div class="mine-home">
    <header-bar :isBorder='false'>
      <div slot="right-actions">设置</div>
    </header-bar>
    <div class="view">
      <div class="top">
        <img class="user-img" src="../static/img/user.png" alt="">
        <div class="nikename">mish</div>
      </div>
      <div class="button">
        <div class="login" v-if="!cellphone" @click="login()">登陆</div>
        <div class="logout" v-if="cellphone" @click="logout()">退出登陆</div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'

export default {
  data () {
    return {
    }
  },
  components: {
    HeaderBar
  },
  computed: {
    cellphone () {
      return localStorage.getItem('cellphone')
    }
  },
  methods: {
    login () {
      this.$router.push({
        path: '/login-or-register',
        query: {
          title: '登陆'
        }
      })
    },
    logout () {
      this.$router.push('/start')
      localStorage.removeItem('cellphone')
      localStorage.removeItem('firstComing')
    }
  }
}
</script>

<style scoped lang="scss">
  .mine-home {
    width: 100%;
    height: 100%;
    .view {
      position: absolute;
      top: 64px;
      width: 100%;
      height: calc(100% - 108px);
      .top {
        position: absolute;
        top: -44px;
        width: 100%;
        height: 150px;
        background: #eee;
        text-align: center;
        .nikename {
          font-size: 13px;
        }
        .user-img {
          margin-top: 60px;
          width: 50px;
          border-radius: 30px;
        }
      }
      .button {
        position: absolute;
        top: 126px;
        width: 100%;
        color: red;
        text-align: center;
        .login, .logout {
        }
      }
    }
  }
</style>



