<template>
  <div class="product-list">
    <header-bar :title="title">
      <div slot="left-actions" @click="toBack()">-1</div>
    </header-bar>
    <div class="view">
      <img class="banner" :src="'http://meilapp.qiniucdn.com'+imgUrl" alt="" v-show="imgUrl">
      <product-list :items="products"></product-list>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'
import ProductList from './ProductList.vue'
import { getProductList } from '../apis/request/getData.js'

export default {
  data () {
    return {
      title: '',
      data: {}
    }
  },
  components: {
    HeaderBar,
    ProductList
  },
  computed: {
    id () {
      return this.$route.params.id
    },
    imgUrl () {
      if (JSON.stringify(this.data) !== '{}') {
        return this.data.banner_img.img
      }
      return ''
    },
    products () {
      if (JSON.stringify(this.data) !== '{}') {
        return this.data.sub_list[0].products
      }
      return []
    }
  },
  async mounted () {
    const result = await getProductList(this.id)
    this.data = result.data[0]
    this.title = this.data.title
    // console.info(this.data.sub_list[0].products, 'mish result')
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .product-list {
    width: 100%;
    height: 100%;
    .view {
      position: absolute;
      top: 64px;
      width: 100%;
      height: calc(100% - 64px);
      overflow: auto;
      .banner {
        width: 100%;
      }
    }
  }
</style>



