<template>
  <div class="banner-list">
    <div class="swipers">
      <swiper :options="swiperOption" id="swiper">
        <swiper-slide class="swiper-slide" v-for="(item, index) in items" :key="index">
          <div class="item">
            <img v-if="item.img" class="makeup-img" :src="'http://meilapp.qiniucdn.com'+item.img" alt="图片" width="100" onerror="onerror=null;src='http://iph.href.lu/100x100'" @click="toMakeup(index)">
            <img v-if="item.banner_thumb" class="makeup-img" :src="'http://meilapp.qiniucdn.com'+item.banner_thumb" alt="图片" :width="width" onerror="onerror=null;src='http://iph.href.lu/100x100'" @click="toMakeup(index)">
            <div v-if="item.short_name" class="short-name">{{item.short_name}}</div>
            <div v-if="item.title" class="short-name title-text">{{item.title}}</div>
            <div v-if="item.fans_count" class="short-name title-text fans_count">{{item.fans_count}}粉丝</div>
          </div>
        </swiper-slide>
      </swiper>
      <!-- <div class="more-info">{{item.more_tip}} ></div> -->
    </div>
  </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array,
      default () {
        return []
      }
    },
    width: {
      type: Number,
      default () {
        return 100
      }
    }
  },
  data () {
    return {
      swiperOption: {
        width: 100,
        notNextTick: true,
        direction: 'horizontal',
        setWrapperSize: true,
        autoHeight: true,
        observeParents: true,
        freeMode: true
      }
    }
  },
  mounted () {
    // this.swiperOption.width = 100
  }
}
</script>

<style scoped lang="scss">
  .banner-list {
    .makeup-img {
      // border-radius: 100px;
    }
    .swipers {
      width: 100%;
      #swiper{
        padding-bottom: 12px;
        height: 139px;
      }
      .swiper-slide{
        padding-right: 10px;
        .title-text {
          font-size: 12px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
        .item {
          display: flex;
          flex-flow: column;
          justify-content: center;
          align-items: center;
          .short-name {
            width: 90px;
            font-size: 12px;
            text-align: center;
          }
          .fans_count {
            color: gray;
          }
        }
      }
      .more-info {
        width: 100%;
        height: 44px;
        line-height: 44px;
        text-align: center;
        color: pink;
        font-size: 14px;
        border-top: 1px solid #DDD;
        border-bottom: 10px solid #eee;
      }
    }
  }
</style>



