<template>
  <div class="">
    <div class="bg-imgs" v-show="items.length > 0">
      <img class="bg-img" v-for="item in items" :key="item.jump_label" :src="'http://meilapp.qiniucdn.com'+item.bg_img" alt="">
    </div>
  </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array,
      default () {
        return []
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .bg-imgs {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    padding-top: 10px;
    box-sizing: border-box;
    .bg-img {
      padding-left: 2.5%;
      width: 30%;
      height: 30%;
    }
  }
</style>



