<template>
  <div class="login">
    <div @click="back()" class="cancel">取消</div>
    <div class="login-item">
      <img class="meila-logo" src="http://meilapp.qiniucdn.com/resource/web/images/meila_logo4-116a708e24.png" alt="">
      <!-- <p class="meila-logo">美啦</p> -->
      <div class="cellphone">
        <div class="number">手机号: <input maxlength="11" placeholder="请输入11位手机号" type="text" v-model="cellphone" @focus="initTestNum()"></div>
        <div class="number">密码: <input @focus="initTestPassWord()" maxlength="12" placeholder="请输入8~12位密码" type="text" v-model="password"></div>
        <div class="point-out" v-show="!isCorrectNum && isTestNum">请输入正确的11位手机号</div>
        <div class="point-out" v-show="!isPassWord && isTestPassWord">请输入8~12位密码</div>
        <div class="code-item" v-if="title === '注册'">
          <div class="code">验证码: <input maxlength="5" placeholder="点击图片换一换" type="text" v-model="code"></div>
          <div class="image-code" v-html="svg" @click="testsvg()"></div>
        </div>
      </div>
      <button v-if="title === '注册'" class="login-botton" @click="register()">注册</button>
      <div v-if="title === '登陆'">
        <button class="login-botton" :disabled="!cellphone || !password || !isAgree" @click="login()">登录</button>
        <form class="agree">
          <div><input type="checkbox" name="agree" v-model="isAgree" value="" />同意 <span>《美啦服务协议》</span></div>
        </form>
      </div>
    </div>
    <div class="alert" v-show="isShowAlert">
      <div class="text">
        <div class="title">{{prompt}}</div>
        <div class="ok" @click="hideAlert()">确定</div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'
import { getImagesCode, register, login } from '../apis/request/getData.js'

export default {
  data () {
    return {
      title: '注册',
      cellphone: '',
      password: '',
      code: '',
      isCorrectNum: false,
      isPassWord: false,
      isAgree: false,
      isShowAlert: false,
      getCodeText: '获取验证码',
      countdown: 60,
      isTestNum: false,
      isTestPassWord: false,
      test: {},
      svg: '',
      prompt: ''
    }
  },
  components: {
    HeaderBar
  },
  async mounted () {
    this.title = this.$route.query.title
    if (this.title === '注册') {
      this.test = await getImagesCode()
      this.svg = this.test.data.img
    }
  },
  methods: {
    async testsvg () {
      this.test = await getImagesCode()
      this.svg = this.test.data.img
    },
    hideAlert () {
      this.isShowAlert = false
    },
    isCorrectCellphone () {
      this.isTestNum = true
      const regx = RegExp('^1[3578]\\d{9}$')
      this.isCorrectNum = regx.test(this.cellphone)
    },
    isCorrectPassword () {
      this.isTestPassWord = true
      this.isPassWord = this.password.length >= 8
    },
    initTestNum () {
      this.isCorrectNum = false
      this.isTestNum = false
    },
    initTestPassWord () {
      this.isCorrectPassWord = false
      this.isTestPassWord = false
    },
    async login () {
      this.isCorrectCellphone()
      this.isCorrectPassword()
      if (this.isCorrectNum && this.isPassWord) {
        const user = {
          cellphone: this.cellphone,
          password: this.password
        }
        const result = await login(user)
        if (result.data.ret === 1) {
          localStorage.setItem('cellphone', this.cellphone)
          this.$router.push('/beauty-home/goods-bask')
        } else {
          this.isShowAlert = true
          this.prompt = result.data.msg
        }
      }
    },
    async register () {
      this.isCorrectCellphone()
      this.isCorrectPassword()
      if (this.isCorrectNum && this.isPassWord && this.code) {
        const user = {
          cellphone: this.cellphone,
          password: this.password,
          code: this.code
        }
        const result = await register(user)
        if (result.data.ret === 1) {
          this.isShowAlert = true
          this.prompt = result.data.msg
          setTimeout(() => {
            this.isShowAlert = false
            this.title = '登陆'
            this.password = ''
          }, 1000)
        } else {
          this.isShowAlert = true
          this.prompt = result.data.msg
        }
      }
    },
    back () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .login {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    .cancel {
      position: absolute;
      top: 30px;
      left: 15px;
      color: pink;
      font-size: 13px;
    }
    input, button {
      border: none;
      outline: medium;
    }
    background-color: white;
    .login-item {
      position: absolute;
      top: 64px;
      width: 100%;
      text-align: center;
      .meila-logo {
        width: 65px;
        margin-top: 50px;
        margin-bottom: 30px;
      }
      .cellphone {
        position: relative;
        color: #999999;
        input {
          padding-left: 20px;
        }
        .point-out {
          position: absolute;
          top: 28px;
          right: 10px;
          font-size: 12px;
          color: red;
        }
        .code-item {
          position: relative;
          .image-code {
            position: absolute;
            bottom: 0;
            right: 25px;
            width: 110px;
            height: 45px;
            border: pink solid 1px;
            background: pink;
          }
        }
        .number, .code {
          height: 60px;
          line-height: 75px;
          border-bottom: 0.5px solid pink;
          width: 260px;
          margin: 0 auto;
          text-align: left;
          font-size: 13px;
          color: pink;
          input {
            color: gray;
          }
        }
        .get-code {
          font-size: 13px;
          width: 90px;
          height: 25px;
          line-height: 25px;
          border-radius: 15px;
          border: 0.5px solid #999999;
          background-color: white;
          position: absolute;
          top: 67px;
          right: 60px;
          color: #999;
        }
        .text-color {
          color: black;
        }
      }
      .login-botton {
        width: 260px;
        height: 30px;
        line-height: 30px;
        background-color: pink;
        color: white;
        border-radius: 15px;
        margin-top: 50px;
      }
      .agree {
        margin-top: 80px;
        span {
          color: #FF6699;
        }
      }
    }
    .alert {
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
      display: flex;
      justify-content: center;
      align-items: center;
      .text {
        width: 260px;
        height: 100px;
        background-color: #FFFFFF;
        text-align: center;
        border-radius: 10px;
        font-size: 15px;
        .title {
          height: 60px;
          line-height: 60px;
          border-bottom: 0.5px solid #CCCCCC;
        }
        .ok {
          height: 40px;
          line-height: 40px;
        }
      }
    }
  }
</style>



