<template>
  <div class="tabs">
    <div class="bottom-bar-button"
      :key="tab.tag"
      v-for="tab in tabs"
      :class="{'selected': current === tab.tag}"
      @click="toTap(tab.tag)">
      <div v-html="tab.icon" class="tab-img"></div>
      <div class="label">{{tab.label}}</div>
    </div>
  </div>
</template>

<script>
import SvgIcon from './SvgIcon.vue'

export default {
  props: {
    tabs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  components: {
    SvgIcon
  },
  data () {
    return {
      current: 0
    }
  },
  methods: {
    toTap (tag) {
      console.info('mish click')
      this.current = tag
      this.$emit('click-bottom', tag)
    }
  }
}
</script>

<style scoped lang="scss">
  .tabs {
    display: flex;
    flex-flow: row nowrap;
  }
  .selected {
    .tab-img, .label {
      color: #FF80AB !important;
    }
  }
  .bottom-bar-button {
    margin: 0 auto;
    border: 0;
    width: 90px;
    .label {
      font-size: 10px;
      text-align: center;
    }
    .tab-img {
      margin: 0 auto;
      display: block;
      color: #9E9E9E;
    }
    .label {
      -webkit-text-size-adjust: none;
      font-size: 10px;
      -webkit-transform: scale(0.9);
      color: #9E9E9E;
    }
    .tab-img {
      width: 23px;
    }
  }
</style>



