<template>
  <div class="percentage">
    <div class="star" v-for="(item, index) in stars" :key="index" :class="{'padding': padding}">
      <span class="title" :style="{ 'width': width + 'px' }">{{item.title}}</span>
      <span v-show="showPercent" class="percent" :style="{ 'width': item.value * 100 + 'px' }" :class="{ 'block': item.value === 0 }"></span>
      <span class="value">{{item.value | valueFilter}}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    stars: {
      type: Array,
      default () {
        return []
      }
    },
    showPercent: {
      type: Boolean,
      default () {
        return true
      }
    },
    padding: {
      type: Boolean,
      default () {
        return false
      }
    },
    width: {
      type: Number
      // default () {
      //   return
      // }
    }
  },
  filters: {
    valueFilter (item) {
      return `${parseInt(item * 100)}%`
    }
  }
}
</script>

<style scoped lang="scss">
  .percentage {
    display: inline-block;
    .percent, .value {
      color: #FF80AB;
    }
    .padding {
      padding-right: 20px;
    }
    .block {
      width: 3px !important;
      background-color: #ddd !important;
    }
    .star {
      padding-bottom: 5px;
      font-size: 13px;
      .title {
        display: inline-block;
      }
      .percent {
        margin-left: 10px;
        margin-right: 5px;
        width: 100px;
        background-color: #FF80AB;
        height: 8px;
        display: inline-block;
        border-radius: 10px;
      }
    }
  }
</style>



