<template>
  <div class="ranking-list-home">
    <div class="search">
      <img src="../static/img/search.png" alt="">
      <input ref="searchMain" type="search" @keyup.13="search($event)" placeholder="搜产品/功效/品牌">
    </div>
    <div class="view">
      <div class="top-custom" v-show="custom.length > 0">
        <img :src="'http://meilapp.qiniucdn.com'+img.img.img" alt="" v-for="(img, index) in custom" :key="index">
      </div>
      <div class="total-des"><span>{{totalDes}}</span>件收录，国内<span>最大</span>的妆品信息库</div>
      <div class="categories">
        <banner-list :items="categories"></banner-list>
      </div>
      <div v-if="featureShow.length > 0" class="feature-show" v-for="(feature, index) in featureShow" :key="index">
        <img width="100%"
          :src="'http://meilapp.qiniucdn.com'+feature.img.img" alt=""
          @click="toProductItems(feature.slug)">
        <banner-list :items="feature.products"></banner-list>
      </div>
      <div class="feature-more">{{featureMoreTitle}} ></div>
      <div class="country-title">. {{countryTitle}} .</div>
      <div v-show="nations.length > 0" class="nations" v-for="nation in nations" :key="nation.slug">
        <img @click="toProductItems(nation.slug)" width="100%" :src="'http://meilapp.qiniucdn.com'+nation.img.img" alt="">
        <banner-list :items="nation.products"></banner-list>
      </div>
    </div>
  </div>
</template>

<script>
import { getRankingListData } from '../apis/request/getData.js'
import BannerList from '../components/BannerList.vue'

export default {
  data () {
    return {
      data: {},
      isDataLoaded: false
    }
  },
  components: {
    BannerList
  },
  computed: {
    custom () {
      if (this.isDataLoaded) {
        return this.data.top.custom
      }
      return []
    },
    totalDes () {
      if (this.isDataLoaded) {
        return this.data.total_des.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,')
      }
      return ''
    },
    categories () {
      if (this.isDataLoaded) {
        return this.data.categories
      }
      return []
    },
    featureShow () {
      if (this.isDataLoaded) {
        return this.data.feature_show
      }
      return []
    },
    featureMoreTitle () {
      if (this.isDataLoaded) {
        return this.data.feature_more.title
      }
      return ''
    },
    countryTitle () {
      if (this.isDataLoaded) {
        return this.data.top.country_title
      }
      return ''
    },
    nations () {
      if (this.isDataLoaded) {
        return this.data.nations
      }
      return []
    }
  },
  async mounted () {
    const result = await getRankingListData()
    this.data = result.data[0]
    this.isDataLoaded = JSON.stringify(this.data) !== '{}'
    console.info('ranking-list-home', result.data[0])
  },
  methods: {
    toProductItems (item) {
      this.$router.push(`/product-items/${item}`)
    }
  }
}
</script>

<style scoped lang="scss">
  .ranking-list-home {
    position: absolute;
    width: 100%;
    height: calc(100% - 64px);
    .search {
      position: absolute;
      top: 0;
      height: 44px;
      width: 100%;
      line-height: 44px;
      color: gray;
      font-size: 14px;
      img {
        position: absolute;
        top: 15px;
        left: 25px;
      }
      input {
        border: 1px solid #eee;
        width: 320px;
        height: 30px;
        border-radius: 20px;
        vertical-align: middle;
        padding-left: 35px;
        margin-left: 15px;
        background-color: rgba(136, 136, 136, 0.1);
        outline: medium;
      }
    }
    .view {
      position: absolute;
      width: 100%;
      top: 44px;
      height: calc(100% - 44px);
      overflow: auto;
    }
    .top-custom {
      display: flex;
      flex-flow: row nowrap;
      padding-top: 10px;
      background-color: rgba(136, 136, 136, 0.1);
      img {
        width: 47%;
        height: 47%;
        padding-left: 2%;
      }
    }
    .total-des {
      width: 100%;
      line-height: 50px;
      height: 50px;
      text-align: center;
      background-color: rgba(136, 136, 136, 0.1);
      font-size: 12px;
      color: gray;
      span {
        color: black;
        font-weight: 600;
      }
    }
    .categories {
      background-color: rgba(136, 136, 136, 0.1)
    }
    .feature-more, .country-title {
      width: 100%;
      height: 50px;
      line-height: 50px;
      font-size: 14px;
      text-align: center;
    }
    .feature-more {
      color: pink;
    }
    .country-title {
      background-color: rgba(136, 136, 136, 0.1)
    }
  }
</style>



