<template>
  <div class="product" @click="toDetail()">
    <img class="banner" :width="width" v-if="product.banner" :src="'http://meilapp.qiniucdn.com'+product.banner" alt="">
    <img class="banner-thumb" :width="width" v-else :src="'http://meilapp.qiniucdn.com'+product.banner_thumb" alt="">
    <div :class="{'text': !border, 'thum-text': border}">
      <div class="short-name">{{product.short_name}}</div>
      <span class="star">{{product.ext.star}}</span>
      <span class="price">参考价 ￥{{product.price}}</span>
      <div class="comment-text">{{product.comment_text}}</div>
      <div class="special">{{product.special}}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    product: {
      type: Object,
      default () {
        return {}
      }
    },
    border: {
      type: Boolean,
      default () {
        return true
      }
    },
    width: {
      type: Number,
      default () {
        return 140
      }
    }
  },
  methods: {
    toDetail () {
      this.$router.push({
        path: '/product-detail',
        query: {
          id: this.product.slug
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .product {
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    .banner {
      // width: 20%;
      // height: 20%;
    }
    .banner-thumb {
      // width: 140px;
      height: 140px;
    }
    .text {
      width: 200px;
      padding: 15px 15px 0 15px;
    }
    .thum-text {
      padding: 20px 10px 40px 0;
      box-sizing: border-box;
      width: calc(100% - 140px);
      border-bottom: 0.5px solid #ddd;
    }
    .comment-text {
      font-size: 11px;
      color: gray;
      // width: 260px;
      white-space: nowrap;
      overflow:hidden;
      text-overflow:ellipsis;
      line-height: 20px;
    }
    .price, .special {
      font-size: 12px;
      color: gray;
    }
    .special {
      padding-top: 15px;
      text-overflow: ellipsis;
      white-space: normal;
      box-sizing: border-box;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
    }
    .short-name {
      padding-bottom: 5px;
      font-size: 13px;
      line-height: 20px;
      font-family: MingLiU;
      color: #616161;
      letter-spacing: 1px;
    }
  }
</style>



