<template>
  <div class="makeup-header">
    <img :src="leftImg" alt="">
    <div class="desc">
      <div class="title">{{title}}</div>
      <div class="num">圈成员 {{count}}</div>
    </div>
    <img class="add" :src="rightImg" alt="">
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default () {
        return ''
      }
    },
    leftImg: {
      type: String,
      default () {
        return ''
      }
    },
    rightImg: {
      type: String,
      default () {
        return ''
      }
    },
    count: {
      type: [ Number, String ],
      default () {
        return 0
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .makeup-header {
    width: 100%;
    height: 70px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    color: gray;
    font-size: 12px;
    padding-left: 10px;
    box-sizing: border-box;
    .left-img {
      width: 38px;
      height: 38px;
    }
    .desc {
      margin-left: 20px;
      line-height: 20px;
    }
    .title {
      font-size: 13px;
    }
    .add {
      margin-left: 180px;
      width: 30px;
      height: 30px;
    }
  }
</style>



