<template>
  <div class="makeUp-list">
    <div class="vtalks">
      <div class="vtalk" v-for="(vtalk, index) in items" :key="index" @click="toDetail(vtalk.slug)">
        <img class="vtalk-img" v-if="!vtalk.img" :src="'http://meilapp.qiniucdn.com'+vtalk.imgs[0].img" alt="">
        <img class="vtalk-img" v-else :src="'http://meilapp.qiniucdn.com'+vtalk.img.img" alt="">
        <div class="title">{{vtalk.title}}</div>
        <div class="img-bottom">
          <img class="avatar" v-if="vtalk.actor" :src="'http://meilapp.qiniucdn.com'+vtalk.actor.user.avatar" alt="">
          <img class="avatar" v-else :src="'http://meilapp.qiniucdn.com'+vtalk.user.avatar" alt="">
          <div class="nickname" v-if="vtalk.actor">{{vtalk.actor.user.nickname}}</div>
          <div class="nickname" v-else>{{vtalk.user.nickname}}</div>
          <div class="like_info">{{vtalk.like_info.like_count}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    toDetail (item) {
      this.$router.push({
        path: '/beauty-talk',
        query: {
          id: item
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .vtalks {
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    .vtalk {
      width: 45%;
      padding-left: 3.3%;
      .title {
        text-overflow:ellipsis;
        white-space: normal;
        width: 165px;
        height: 40px;
        box-sizing: border-box;
        line-height: 20px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        font-size: 12px;
      }
      .vtalk-img {
        width: 170px;
      }
      .img-bottom {
        position: relative;
        margin-top: 5px;
        margin-bottom: 15px;
        display: flex;
        flex-flow: row nowrap;
        align-items: center;
        font-size: 12px;
        color: gray;
        .avatar {
          width: 30px;
          height: 30px;
          border-radius: 15px;
          margin-right: 5px;
        }
        .like_info {
          position: absolute;
          right: 0;
        }
      }
    }
  }
</style>



