<template>
  <svg class="icon">
    <use :xlink:href="uid"></use>
  </svg>
</template>

<script>
// icons
import '../assets/icons/event.svg'
import '../assets/icons/settings.svg'
import '../assets/icons/add.svg'
import '../assets/icons/play_arrow.svg'
import '../assets/icons/save.svg'
import '../assets/icons/delete.svg'
import '../assets/icons/chevron_right.svg'
import '../assets/icons/chevron_left.svg'
import '../assets/icons/close.svg'
import '../assets/icons/check_circle.svg'
import '../assets/icons/search.svg'
import '../assets/icons/cancel.svg'
import '../assets/icons/print.svg'
import '../assets/icons/done.svg'
import '../assets/icons/refresh.svg'
import '../assets/icons/face.svg'
import '../assets/icons/autorenew.svg'
import '../assets/icons/wallpaper.svg'
import '../assets/icons/info_outline.svg'
import '../assets/icons/fiber_manual_record.svg'
import '../assets/icons/cached.svg'
import '../assets/icons/panorama_fish_eye.svg'
import '../assets/icons/update.svg'
import '../assets/icons/system_update.svg'
import '../assets/icons/account_circle.svg'
import '../assets/icons/chat.svg'
import '../assets/icons/account_box.svg'
import '../assets/icons/tag_faces.svg'
import '../assets/icons/chat_done.svg'
import '../assets/icons/user.svg'
import '../assets/icons/package.svg'
import '../assets/icons/info_alt.svg'
import '../assets/icons/more.svg'
import '../assets/icons/service_crew_member.svg'
import '../assets/icons/debug.svg'

// images
import '../assets/images/no_data.svg'
import '../assets/images/charket_logo.svg'
import '../assets/images/charket_logo_short.svg'

export default {
  props: {
    name: {
      type: String,
      default () {
        return ''
      }
    }
  },
  computed: {
    uid () {
      return `#${this.name}`
    }
  },
  mounted () {
    console.info('mish', this.name)
  }
}
</script>

<style scoped lang="scss">
  .icon {
    width: 24px;
    min-width: 24px;
    height: auto;
    min-height: 24px;
    margin: auto;
    align-items: center;
    vertical-align: middle;
    font-weight: normal;
    font-style: normal;
    font-size: 24px;
    fill: currentColor;
    line-height: 1;
    letter-spacing: normal;
    text-transform: none;
    display: inline-block;
    white-space: nowrap;
    word-wrap: normal;
    direction: ltr;
  }
</style>



