<template>
  <div>
    <div class="comments">
      <div class="comment" :class="{ 'border': border }" v-for="item in items" :key="item.slug">
        <div class="user">
          <img class="avatar" :src="'http://meilapp.qiniucdn.com'+item.user.avatar" alt="">
          <div class="text-message">
            <div class="nickname">
              <span :style="{'color': color}">{{item.user.nickname}}</span>
              <span class="level">L{{item.user.level}}</span>
            </div>
            <div class="skin-type" v-if="types.length > 0">
              <span v-if="item.user.age_range">{{item.user.age_range}}</span>
              <span v-else>年龄未知</span>
              <span v-if="item.user.skin_type">{{types[item.user.skin_type - 1] | titleFilter}}</span></div>
          </div>
        </div>
        <div class="content" :style="{'margin-left': left + 'px', 'margin-top': top + 'px'}">
          <pre class="text"><span v-if="item.is_top">精</span>{{item.content}}</pre></div>
        <div class="replies" v-if="item.replies && item.replies.length > 0">
          <div class="reply" v-for="reply in item.replies" :key="reply.slug">
            <span v-if="!reply.reply_to_id" class="nickname">{{reply.user.nickname}}:</span>
            <span v-if="reply.reply_to_id">{{reply.user.nickname}}<span class="responce">回复</span>{{reply.reply_to.user.nickname}}:</span>
            <span class="reply-content">{{reply.content}}</span>
          </div>
        </div>
      </div>
    </div>
    <div class="comments-bottom" @click="toDetail()" v-if="isShow">
      查看全部评论 >
    </div>
  </div>
</template>

<script>
export default {
  props: {
    types: {
      type: Array,
      default () {
        return []
      }
    },
    items: {
      type: Array,
      default () {
        return []
      }
    },
    border: {
      type: Boolean,
      default () {
        return false
      }
    },
    isShow: {
      type: Boolean,
      default () {
        return false
      }
    },
    left: {
      type: Number,
      default () {
        return 50
      }
    },
    top: {
      type: Number,
      default () {
        return 0
      }
    },
    color: {
      type: String,
      default () {
        return 'gray'
      }
    }
  },
  mounted () {
    // console.info(this.types, 'mish types')
  },
  filters: {
    titleFilter (item) {
      return item.title
    }
  },
  methods: {
    toDetail () {
      this.$emit('to-detail')
    }
  }
}
</script>

<style scoped lang="scss">
  .comments {
    .border {
      border-bottom: 0.5px solid #ddd;
    }
    .comment {
      padding: 10px 0 5px 15px;
      .user {
        display: flex;
        font-size: 13px;
        color: gray;
        align-items: center;
        .avatar {
          width: 38px;
          height: 38px;
          border-radius: 20px;
        }
        .text-message {
          display: flex;
          flex-flow: column;
          .skin-type {
            padding-left: 10px;
            padding-top: 3px;
          }
        }
        .nickname {
          margin-left: 10px;
        }
        .level {
          color: white;
          background-color: black;
          margin-left: 5px;
        }
      }
      .content {
        font-size: 13px;
        margin-right: 5px;
        span {
          color: white;
          background-color: #FF80AB;
          font-size: 13px;
          padding: 2px;
          margin-right: 5px;
        }
        .text {
          white-space: pre-wrap;
          font-size: 13px;
          line-height: 20px;
        }
      }
      .replies {
        background-color: #ddd;
        margin-left: 50px;
        font-size: 13px;
        margin-right: 10px;
        margin-top: 5px;
        padding: 5px 10px 5px 5px;
        .reply {
          line-height: 25px;
          span {
            font-weight: 600;
          }
          .responce, .reply-content {
            font-weight: 100;
          }
        }
      }
    }
  }
  .comments-bottom {
    font-size: 16px;
    height: 50px;
    line-height: 50px;
    border-top: 1px solid #ddd;
    border-bottom: 10px solid #ddd;
    text-align: center;
    color: #FF80AB;
  }
</style>



