<template>
  <div class="face-makeup-home">
    <header-bar :title="vtalk.title">
      <div slot="left-actions" @click="toBack()">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
    </header-bar>
    <div class="face-makeup">
      <div class="title">{{video.title}}</div>
      <div class="swipers">
        <swiper :options="swiperOption" id="swiper">
          <swiper-slide class="swiper-slide" v-for="(item, index) in video.videos" :key="index">
            <div class="item">
              <img class="makeup-img" :src="'http://meilapp.qiniucdn.com'+item.img.img" alt="图片" width="100" onerror="onerror=null;src='http://iph.href.lu/100x100'" @click="toMakeup(index)">
              <div class="title-text">{{item.title}}</div>
            </div>
          </swiper-slide>
        </swiper>
        <div class="more-info">{{video.more_tip}} ></div>
      </div>
      <div class="relative-topic">
        <div class="relative-title">{{vtalk.title}}</div>
        <div class="vtalks">
          <div class="vtalk" v-for="(talk, index) in vtalk.vtalks" :key="index">
            <img class="vtalk-img" :src="'http://meilapp.qiniucdn.com'+talk.imgs[0].img" alt="">
            <div class="vtalk-title">{{talk.title}}</div>
            <div class="user">
              <img class="avatar" :src="'http://meilapp.qiniucdn.com'+talk.user.avatar" alt="">
              <div class="nike-name">{{talk.user.nickname}}</div>
              <div class="like">like {{talk.like_info.like_count}}</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../../components/HeaderBar.vue'
import { getMakeupItemData } from '../../apis/request/getData.js'

export default {
  data () {
    return {
      swiperOption: {
        width: 100,
        notNextTick: true,
        direction: 'horizontal',
        setWrapperSize: true,
        autoHeight: true,
        observeParents: true,
        freeMode: true
      },
      video: {},
      vtalk: {}
    }
  },
  computed: {
    tab () {
      return this.$route.query.index
    }
  },
  components: {
    HeaderBar
  },
  async mounted () {
    const result = await getMakeupItemData(this.tab)
    this.video = result.data[0].video
    this.vtalk = result.data[0].vtalk
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .face-makeup-home {
    width: 100%;
    height: 100%;
    .face-makeup {
      position: absolute;
      width: 100%;
      height: calc(100% - 64px);
      top: 64px;
      overflow: auto;
    }
    .title {
      padding-left: 15px;
      height: 44px;
      line-height: 44px;
      font-size: 14px;
    }
    .swipers {
      width: 100%;
      height: 210px;
      #swiper{
        padding-bottom: 12px;
        height: 139px;
      }
      .swiper-slide{
        padding-right: 10px;
        .title-text {
          font-size: 12px;
          height: 35px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
      }
      .more-info {
        width: 100%;
        height: 44px;
        line-height: 44px;
        text-align: center;
        color: pink;
        font-size: 14px;
        border-top: 1px solid #DDD;
        border-bottom: 10px solid #eee;
      }
    }
    .relative-title {
      height: 44px;
      line-height: 44px;
      padding-left: 3%;
    }
    .vtalks {
      display: flex;
      flex-flow: row wrap;
      width: 100%;
      text-align: center;
      .vtalk {
        width: 45.5%;
        padding-left: 3%;
        .vtalk-img {
          width: 100%;
        }
        .user {
          margin-bottom: 10px;
          font-size: 12px;
          color: gray;
          display: flex;
          flex-flow: row nowrap;
          align-items: center;
        }
        .vtalk-title {
          padding-top: 10px;
          margin-bottom: 10px;
          font-size: 12px;
          height: 34px;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          overflow: hidden;
        }
        .avatar {
          width: 28px;
          height: 28px;
          border-radius: 20px;
          margin-right: 5px;
        }
      }
    }
  }
</style>



