<template>
  <div class="beauty-gone">
    <div v-for="(item, index) in goneItems" :key="index" @click="toDetail(item.jump_data)">
      <img class="banner-img" :src="'http://meilapp.qiniucdn.com'+item.banner" alt="">
      <div class="img-bottom">
        <div class="try-count">试用数: {{item.try_count}}份</div>
        <div class="view-count">已参与: {{item.page_view_count}}人</div>
        <div class="offline-time">结束时间: {{item.offline_time}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    goneItems: {
      type: Array,
      default () {
        return []
      }
    }
  },
  methods: {
    toDetail (item) {
      this.$router.push({
        path: '/beauty-talk',
        query: {
          id: item
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
  .beauty-gone {
    .banner-img {
      width: 100%;
    }
    .img-bottom {
      display: flex;
      flex-flow: row nowrap;
      color: gray;
      font-size: 12px;
      padding: 10px 10px;
      border-bottom: 10px solid #EEEEEE;
      .try-count, .view-count {
        margin-right: 20px;
      }
      .offline-time {
        margin-left: 20px;
      }
    }
  }
</style>



