<template>
  <div class="beauty-talk-comments">
    <header-bar :title="title">
      <div slot="left-actions" @click="toBack()">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
    </header-bar>
    <div class="comment-list">
      <comment-list :items="comments" :border="true"></comment-list>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'
import CommentList from '../components/CommentList.vue'
import { getBeautyTalkComments } from '../apis/request/getData.js'

export default {
  data () {
    return {
      title: '全部评论',
      data: {}
    }
  },
  components: {
    HeaderBar,
    CommentList
  },
  computed: {
    id () {
      return this.$route.params.id
    },
    comments () {
      if (JSON.stringify(this.data) !== '{}') {
        return this.data.comments
      }
      return []
    }
  },
  activated () {
    console.info('activated00')
  },
  async mounted () {
    const result = await getBeautyTalkComments(this.id)
    this.data = result.data[0]
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .beauty-talk-comments {
    width: 100%;
    .comment-list {
      width: 100%;
      position: absolute;
      top: 64px;
      height: calc(100% - 108px);
      overflow: auto;
    }
  }
</style>



