<template>
  <div class="start">
    <img src="../static/img/start_makeup.jpg" alt="">
    <div class="button">
      <button @click="toLogin()" class="login">登陆</button>
      <button @click="toRegister()" class="register">注册</button>
    </div>
    <div @click="toHome()" class="text">先不登陆,随便看看></div>
  </div>
</template>

<script>
export default {
  data () {
    return {}
  },
  methods: {
    toLogin () {
      this.$router.push({
        path: '/login-or-register',
        query: {
          title: '登陆'
        }
      })
    },
    toRegister () {
      this.$router.push({
        path: '/login-or-register',
        query: {
          title: '注册'
        }
      })
    },
    toHome () {
      this.$router.push('/beauty-home/goods-bask')
      localStorage.setItem('firstComing', true)
    }
  }
}
</script>

<style scoped lang="scss">
  .start {
    width: 100%;
    height: 100%;
    img {
      position: absolute;
      width: 100%;
      height: 100%;
    }
    .button {
      position: absolute;
      width: 100%;
      height: 35px;
      top: 260px;
      button {
        position: absolute;
        width: 140px;
        height: 35px;
        border-radius: 10px;
        border: none;
        outline: medium;
        background-color: white;
      }
      .login {
        left: 7%;
        border: solid 1px pink;
        color: pink;
      }
      .register {
        right: 7%;
        background-color: pink;
        color: white;
      }
    }
    .text {
      position: absolute;
      top: 320px;
      color: pink;
      font-size: 12px;
      left: 50%;
      transform: translateX(-50%);
    }
  }
</style>



