<template>
  <div class="beauty-videos-detail">
    <header-bar>
      <div slot="left-actions" @click="toBack()">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
      <img class="header-img" :src="'http://meilapp.qiniucdn.com'+img" alt="" v-if="hasData">
    </header-bar>
    <div class="talks">
      <video :src="video.url" controls="controls" autoplay="autoplay" width="100%"></video>
      <div class="text">
        <div class="summary">{{video.summary}}</div>
      </div>
      <div class="products">
        <p class="top">视频中提到</p>
        <div v-for="(item, index) in productsOrWares" class="product" :key="index">
          <product :product="item.product" :border="true"></product>
          <div class="seconds">.{{item.seconds | fliterSeconds}}</div>
        </div>
      </div>
      <user :user="user" :isFollow="isLiked" v-if="hasData"></user>
      <div class="comments-top">
        <div class="time">3个人月前</div>
        <div class="eye">{{visitCount}}</div>
        <div class="comment-count">{{commentCount}}</div>
        <div class="like">{{likeCount}}</div>
      </div>
      <comment-list
        v-if="hasData"
        :items="comments"
        :isShow="commentCount > 5"
        @to-detail="viewComments($event)">
      </comment-list>
      <div class="maybe-intresting">
        <div class="title">- 你可能感兴趣 -</div>
        <makeup-list :items="videos" v-if="videos.length > 0"></makeup-list>
      </div>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'
import CommentList from '../components/CommentList.vue'
import Star from '../components/Star.vue'
import Tags from '../components/Tags.vue'
import User from './User.vue'
import MakeupList from './MakeupList.vue'
import Product from '../rankingList/Product.vue'
import { getVideosDetail } from '../apis/request/getData.js'

export default {
  data () {
    return {
      data: {},
      vtalks: {},
      list1: [],
      list2: [],
      hasData: false
    }
  },
  components: {
    HeaderBar,
    Star,
    MakeupList,
    CommentList,
    User,
    Tags,
    Product
  },
  computed: {
    videos () {
      if (this.hasData) {
        return this.data.recommend_videos.videos
      }
      return []
    },
    comments () {
      if (this.hasData) {
        return this.data.comments
      }
      return []
    },
    likeCount () {
      if (this.hasData) {
        return this.video.like_info.like_count
      }
      return 0
    },
    commentCount () {
      if (this.hasData) {
        return this.video.comment_count
      }
      return 0
    },
    isLiked () {
      if (this.hasData) {
        return this.video.like_info.is_liked
      }
      return false
    },
    user () {
      if (this.hasData) {
        return this.video.actor.user
      }
      return {}
    },
    img () {
      if (this.hasData) {
        return this.video.img.img
      }
      return ''
    },
    id () {
      return this.$route.query.id
    },
    video () {
      if (this.hasData) {
        return this.data.video
      }
      return {}
    },
    productsOrWares () {
      if (this.hasData) {
        return this.video.products_or_wares
      }
      return {}
    },
    visitCount () {
      if (this.hasData) {
        return this.video.visit_count
      }
      return 0
    }
  },
  filters: {
    fliterSeconds (item) {
      return `${item / 60} '00''`
    }
  },
  async mounted () {
    console.info(this.id, 'mish 1212')
    const result = await getVideosDetail(this.id)
    this.data = result.data[0]
    this.hasData = JSON.stringify(this.data) !== '{}'
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    },
    viewComments () {
      this.$router.push(`/beauty-talk-comments/${this.id}`)
    }
  }
}
</script>

<style scoped lang="scss">
  .beauty-videos-detail {
    width: 100%;
    .header-img {
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 35px;
      border-radius: 20px;
    }
    .talks {
      width: 100%;
      position: absolute;
      top: 64px;
      height: calc(100% - 108px);
      overflow: auto;
      .products {
        .product {
          position: relative;
          padding: 10px 0 10px 10px;
          .seconds {
            position: absolute;
            top: 7px;
            font-size: 13px;
          }
        }
        .top {
          margin: 0;
          padding: 15px 10px;
          font-size: 14px;
          border-bottom: 0.5px solid #ddd;
        }
      }
      .text {
        font-size: 12px;
        width: 100%;
        padding: 10px 10px 0 10px;
        box-sizing: border-box;
        .title {
          white-space: pre-wrap;
        }
      }
      .talk {
        .title {
          border-bottom: 0.5px solid #ddd;
          margin: 0;
          padding: 15px;
          font-size: 14px;
        }
      }
      .comments-top {
        display: flex;
        justify-content: center;
        height: 40px;
        line-height: 40px;
        border-bottom: 0.5px solid #ddd;
        font-size: 12px;
        color: gray;
        .time {
          padding-right: 20px;
        }
        .eye {
          // padding-left: 50px;
        }
        .comment-count {
          padding-left: 160px;
        }
        .like {
          padding-left: 20px;
        }
      }
      .maybe-intresting {
        .title {
          height: 50px;
          line-height: 50px;
          text-align: center;
        }
      }
    }
  }
</style>



