<template>
  <div class="home">
    <div class="top-bar"></div>
    <div class="view">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
    <tabs class="bottom-tabs" :tabs="tabs" @click-bottom="toTap($event)"></tabs>
  </div>
</template>

<script>

import tabs from '../components/Tabs.vue'

export default {
  components: {
    tabs
  },
  data () {
    return {
      tabs: [{
        tag: 0,
        label: '变美啦',
        icon: '<i class="fa fa-home fa-lg" aria-hidden="true"></i>'
      }, {
        tag: 1,
        label: '排行榜',
        icon: '<i class="fa fa-diamond fa-lg"></i>'
      }, {
        tag: 2,
        label: '发现',
        icon: '<i class="fa fa-eye fa-lg"></i>'
      }, {
        tag: 3,
        label: '我的',
        icon: '<i class="fa fa-user-o fa-lg" aria-hidden="true"></i>'
      }]
    }
  },
  computed: {
    cellphone () {
      return localStorage.getItem('cellphone')
    },
    firstComing () {
      return localStorage.getItem('firstComing')
    }
  },
  async mounted () {
    if (!this.cellphone && !this.firstComing) {
      this.$router.push('/start')
    } else {
      this.$router.push('/beauty-home/goods-bask')
    }
  },
  methods: {
    toTap (tag) {
      if (tag === 0) {
        this.$router.push('/beauty-home/goods-bask')
      }
      if (tag === 1) {
        this.$router.push('/ranking-list-home')
      }
      if (tag === 2) {
        this.$router.push('/find-home')
      }
      if (tag === 3) {
        this.$router.push('/mine-home')
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .home {
    width: 100%;
    .top-bar {
      height: 20px;
      width: 100%;
    }
    .bottom-tabs {
      position: absolute;
      width: 100%;
      height: 44px;
      bottom: 0;
      box-sizing: border-box;
      padding-top: 8px;
      border-top: 1px solid #ddd;
    }
  }
</style>



