<template>
  <div class="header">
    <div class="top-bar"></div>
    <div class="actions" :class="{ 'set-border': isBorder }">
      <div class="left-actions">
        <slot name="left-actions"></slot>
      </div>
      <h2 class="title">{{title}}</h2>
      <div class="right-actions">
        <slot name="right-actions"></slot>
      </div>
      <slot></slot>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String,
      default () {
        return ''
      }
    },
    isBorder: {
      type: Boolean,
      default () {
        return true
      }
    }
  },
  mounted () {
    console.info('mish header mounted')
  }
}
</script>

<style scoped lang="scss">
  .header {
    .top-bar {
      position: absolute;
      height: 20px;
      width: 100%;
    }
    .set-border {
      border-bottom: 0.5px solid #ddd;
    }
    .actions {
      position: absolute;
      top: 20px;
      width: 100%;
      height: 44px;
      text-align: center;
      box-sizing: border-box;
      .left-actions, .right-actions {
        position: absolute;
      }
      .left-actions {
        left: 10px;
        top: 50%;
        transform: translateY(-50%);
        color: #BDBDBD;
        font-size: 1.7em;
      }
      .right-actions {
        top: 50%;
        right: 10px;
        transform: translateY(-50%);
      }
      .title {
        font-size: 14px;
        font-weight: normal;
      }
    }
  }
</style>



