<template>
  <div>
    <div class="list" v-if="items.length > 0" v-for="(product, index) in items" :key="index">
      <product :product="product" :border="border" :width="width"></product>
    </div>
  </div>
</template>

<script>
import Product from './Product.vue'

export default {
  props: {
    items: {
      type: Array,
      default () {
        return []
      }
    },
    border: {
      type: Boolean,
      default () {
        return true
      }
    },
    width: {
      type: Number,
      default () {
        return 100
      }
    }
  },
  components: {
    Product
  }
}
</script>

<style scoped lang="scss">
  .list {
    // display: flex;
    // flex-flow: row nowrap;
    // align-items: center;
  }
</style>



