import Vue from 'vue'
import Router from 'vue-router'
import Home from '../home/Home.vue'

// import Register from '../components/Register.vue'
import LoginOrRegister from '../components/LoginOrRegister.vue'
import Start from '../components/Start.vue'

import BeautyHome from '../beauty/BeautyHome.vue'
import GoodsBask from '../beauty/GoodsBask.vue'
import MakeUp from '../beauty/MakeUp.vue'
import BeautyDaquan from '../beauty/BeautyDaquan.vue'
import BeautyTalk from '../beauty/BeautyTalk.vue'
import BeautyVideosDetail from '../beauty/BeautyVideosDetail.vue'
import BeautyTalkComments from '../beauty/BeautyTalkComments.vue'
import MakeupTopic from '../beauty/MakeupTopic.vue'
import BeautyVideos from '../beauty/BeautyVideos.vue'
import Follow from '../beauty/Follow.vue'

import DressingMakeupHome from '../beauty/dressingMakeup/DressingMakeupHome.vue'

import FaceMakeupHome from '../beauty/faceMakeup/FaceMakeupHome.vue'

import BeautyTrailHome from '../beauty/beautyFreeTaril/BeautyTrailHome.vue'

import RankingListHome from '../rankingList/RankingListHome.vue'
import ProductItems from '../rankingList/ProductItems.vue'
import ProductDetail from '../rankingList/ProductDetail.vue'
import ProductInstruction from '../rankingList/ProductInstruction.vue'

import FindHome from '../find/FindHome.vue'

import MineHome from '../mine/MineHome.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
      children: [{
        path: '/beauty-home',
        component: BeautyHome,
        name: 'BeautyHome',
        children: [{
          path: '/beauty-home/goods-bask',
          component: GoodsBask,
          name: 'GoodsBask'
        }, {
          path: '/beauty-home/make-up',
          component: MakeUp,
          name: 'MakeUp'
        }, {
          path: '/beauty-home/follow',
          component: Follow,
          name: 'Follow'
        }]
      }, {
        path: '/ranking-list-home',
        component: RankingListHome,
        name: 'RankingListHome'
      }, {
        path: '/mine-home',
        name: 'MineHome',
        component: MineHome
      }, {
        path: '/find-home',
        component: FindHome,
        name: 'FindHome'
      }]
    }, {
      path: '/beauty-daquan',
      component: BeautyDaquan,
      name: 'BeautyDaquan'
    }, {
      path: '/beauty-talk',
      component: BeautyTalk,
      name: 'BeautyTalk'
    }, {
      path: '/beauty-videos-detail',
      component: BeautyVideosDetail,
      name: 'BeautyVideosDetail'
    }, {
      path: '/beauty-talk-comments/:id',
      component: BeautyTalkComments,
      name: 'BeautyTalkComments'
    }, {
      path: '/beauty-trail-home',
      component: BeautyTrailHome,
      name: 'BeautyTrailHome'
    }, {
      path: '/makeup-topic',
      component: MakeupTopic,
      name: 'MakeupTopic'
    }, {
      path: '/beauty-videos',
      component: BeautyVideos,
      name: 'BeautyVideos'
    }, {
      path: '/dressing-makeup-home',
      component: DressingMakeupHome,
      name: 'DressingMakeupHome'
    }, {
      path: '/face-makeup-home',
      component: FaceMakeupHome,
      name: 'FaceMakeupHome'
    }, {
      path: '/product-items/:id',
      component: ProductItems,
      name: 'ProductItems'
    }, {
      path: '/product-detail',
      component: ProductDetail,
      name: 'ProductDetail'
    }, {
      path: '/product-instruction',
      components: ProductInstruction,
      name: 'ProductInstruction'
    }, {
      path: '/login-or-register',
      name: 'LoginOrRegister',
      component: LoginOrRegister
    }, {
      path: '/start',
      name: 'Start',
      component: Start
    }]
})



