<template>
  <div class="goods-bask">
    <swiper :options="swiperOption" id="swiper" v-if="banners.length > 0">
      <swiper-slide @click.native="toDetail(banners[1].jump_data, 1)">
        <img :src="'http://meilapp.qiniucdn.com'+banners[1].img" alt="banner" width="100%" onerror="onerror=null;src='http://iph.href.lu/414x155'">
      </swiper-slide>
      <swiper-slide v-for="(item, index) in banners" :key="item.jump_data" @click.native="toDetail(item.jump_data, index)">
        <img :src="'http://meilapp.qiniucdn.com'+item.img" alt="banner" width="100%" onerror="onerror=null;src='http://iph.href.lu/414x155'">
      </swiper-slide>
      <swiper-slide @click.native="toDetail(banners[0].jump_data, 0)">
        <img :src="'http://meilapp.qiniucdn.com'+banners[0].img" alt="banner" width="100%" onerror="onerror=null;src='http://iph.href.lu/414x155'">
      </swiper-slide>
      <div class="swiper-pagination"  slot="pagination"></div>
    </swiper>
    <div class="hot-activity">
      <div class="line-row"></div>
      <div class="line-column"></div>
      <div class="activity" v-if="entries.length > 0" v-for="(entrie, index) in entries" :key="index" @click="toTap(index)">
        <img :src="'http://meilapp.qiniucdn.com' + entrie.img" alt="" width="38px">
        <div class="text">
          <div class="title">{{entrie.title}}</div>
          <div class="desc">{{entrie.desc}}</div>
        </div>
      </div>
    </div>
    <div class="user-topic-list">
      <beauty-list :items="feeds"></beauty-list>
    </div>
  </div>
</template>

<script>
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import { getHomeData } from '../apis/request/getData.js'
import BeautyList from './BeautyList.vue'

export default {
  data () {
    return {
      imgUrl: '',
      data: {},
      entries: [],
      feeds: [],
      banners: [],
      swiperOption: {
        // http://www.swiper.com.cn/api/
        preventClicks: false,
        notNextTick: true,
        speed: 600,
        autoplay: {
          delay: 3000,
          stopOnLastSlide: false,
          disableOnInteraction: true,
          reverseDirection: false
        },
        direction: 'horizontal',
        grabCursor: true,
        setWrapperSize: true,
        autoHeight: true,
        pagination: '.swiper-pagination',
        paginationClickable: true,
        preventLinksPropagation: true,
        loop: true,
        observer: true // 修改swiper自己或子元素时，自动初始化swipe
      }
    }
  },
  components: {
    BeautyList,
    swiper,
    swiperSlide
  },
  computed: {
    //
  },
  async mounted () {
    const result = await getHomeData()
    this.data = result.data[1]
    this.entries = this.data.entries
    this.banners = this.data.banners
    this.feeds = this.data.feeds
  },
  methods: {
    toBeautyTalk () {
      console.info('toBeautyTalk')
      this.$router.push('/beauty-talk')
    },
    toTap (index) {
      console.info(index, 'mish index')
      if (index === 0) {
        this.$router.push('/beauty-trail-home')
      }
      if (index === 2) {
        this.$router.push('/makeup-topic')
      }
      if (index === 3) {
        this.$router.push('/beauty-videos')
      }
    },
    toDetail (item, index) {
      if (index === 1) {
        this.$router.push(`/product-list/${item}`)
      }
      if (index === 0) {
        this.$router.push({
          path: '/beauty-talk',
          query: {
            id: item
          }
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
  #swiper{
    width: 100%;
    height: 190px;
    background-color: #7e8082;
  }
  .banner-img {
    width: 100%;
  }
  .hot-activity {
    position: relative;
    margin-top: -5px;
    width: 100%;
    display: flex;
    flex-flow: row wrap;
    border-top: 10px solid #EEEEEE;
    padding-bottom: 10px;
    .line-column {
      position: absolute;
      left: 50%;
      transfrom: translateX(-50%);
      height: 100%;
      width: 1px;
      background-color: #DDDDDD;
    }
    .line-row {
      position: absolute;
      top: calc(50% + 5px);
      transfrom: translateY(-50%);
      width: 100%;
      height: 1px;
      background-color: #DDDDDD;
    }
    .activity {
      width: 50%;
      padding-top: 15px;
      display: flex;
      align-items: center;
      justify-content: center;
      box-sizing: border-box;
      .text {
        text-align: left;
        .title {
          color: black;
          font-size: 12px;
        }
        .desc {
          color: gray;
          font-size: 10px;
        }
      }
    }
  }
  .user-topic-list {
    width: 100%;
    position: relative;
  }
</style>



