<template>
  <div class="find-home">
    <div class="search">
      <img src="../static/img/search.png" alt="">
      <input ref="searchMain" type="search" @keyup.13="search($event)" placeholder="搜产品/功效/品牌">
    </div>
    <div class="view">
      <bg-img :items="topList"></bg-img>
      <div class="tags" v-show="recommendTagList.length > 0">
        <div class="tag" v-for="tag in recommendTagList" :key="tag.jump_data">
          {{tag.title}}
        </div>
      </div>
      <div class="list" v-show="subjectList.length > 0">
        <div class="subject" v-for="subject in subjectList" :key="subject.jump_data">
          <div v-if="!subject.img.length > 0">
            <div class="title">{{subject.title}}</div>
            <div class="desc">{{subject.desc}}</div>
            <div class="imgs">
              <div class="img-item" v-for="(topic, index) in subject.topics" :key="index">
                 <img class="img" :src="'http://meilapp.qiniucdn.com'+topic.imgs[0].img" alt="">
                <img class="avatar" :src="'http://meilapp.qiniucdn.com'+topic.user.avatar" alt="">
              </div>
            </div>
          </div>
          <div v-if="subject.img.length > 0"><img class="banner" :src="'http://meilapp.qiniucdn.com'+subject.img[0].img" alt=""></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import BgImg from './BgImg.vue'
import { getFindData } from '../apis/request/getData.js'

export default {
  data () {
    return {
      isDataLoaded: false
    }
  },
  components: {
    BgImg
  },
  computed: {
    topList () {
      if (this.isDataLoaded) {
        return this.data.top_list
      }
      return []
    },
    recommendTagList () {
      if (this.isDataLoaded) {
        return this.data.recommend_tag_list
      }
      return []
    },
    subjectList () {
      if (this.isDataLoaded) {
        return this.data.subject_list
      }
      return []
    }
  },
  async mounted () {
    const result = await getFindData()
    this.data = result.data[0]
    this.isDataLoaded = JSON.stringify(this.data) !== '{}'
    console.info(result.data[0].subject_list, 'mish result data')
  }
}
</script>

<style scoped lang="scss">
  .find-home {
    position: absolute;
    width: 100%;
    height: calc(100% - 64px);
    .search {
      position: absolute;
      top: 0;
      height: 44px;
      width: 100%;
      line-height: 44px;
      color: gray;
      font-size: 14px;
      border-bottom: 1px solid #eee;
      img {
        position: absolute;
        top: 15px;
        left: 25px;
      }
      input {
        border: 1px solid #eee;
        width: 320px;
        height: 30px;
        border-radius: 20px;
        vertical-align: middle;
        padding-left: 35px;
        margin-left: 15px;
        background-color: rgba(136, 136, 136, 0.1);
        outline: medium;
      }
    }
    .view {
      position: absolute;
      width: 100%;
      top: 44px;
      height: calc(100% - 44px);
      overflow: auto;
      .tags {
        width: 100%;
        display: flex;
        flex-flow: row wrap;
        border-bottom: 10px solid #ddd;
        padding-top: 5px;
        padding-bottom: 15px;
        .tag {
          margin-top: 10px;
          margin-left: 4%;
          width: 20%;
          height: 30px;
          border: 1px solid #ddd;
          border-radius: 10px;
          font-size: 13px;
          line-height: 30px;
          text-align: center;
          box-sizing: border-box;
        }
      }
      .list {
        .subject {
          .title {
            width: 100%;
            height: 30px;
            line-height: 30px;
            font-size: 23px;
            margin-top: 20px;
            text-align: center;
          }
          .desc {
            width: 100%;
            height: 20px;
            line-height: 20px;
            color: gray;
            font-size: 14px;
            text-align: center;
            word-spacing: 2px;
            margin-bottom: 20px;
          }
          .imgs {
            position: relative;
            width: 100%;
            display: flex;
            flex-flow: row nowrap;
            margin-bottom: 30px;
            .img-item {
              position: relative;
              margin-left: 2.4%;
              width: 22%;
              height: 22%;
              box-sizing: border-box;
              .avatar {
                position: absolute;
                left: 50%;
                transform: translateX(-50%);
                bottom: -13px;
                width: 30px;
                height: 30px;
                border-radius: 20px;
              }
            }
            .img {
              // margin-left: 4%;
              width: 100%;
              height: 100%;
            }
            img {
              width: 50px;
            }
          }
          .banner {
            width: 100%;
          }
        }
      }
    }
  }
</style>



