<template>
  <div class="follow">
    <div class="recommend">
      <div class="title">- {{title}} -</div>
      <banner-list :items="recommends"></banner-list>
    </div>
    <div class="user-topic-list">
      <beauty-list :items="feeds"></beauty-list>
    </div>
  </div>
</template>

<script>
import { getFollowData } from '../apis/request/getData.js'
import BeautyList from './BeautyList.vue'
import BannerList from '../components/BannerList.vue'
export default {
  data () {
    return {
      data: {},
      isDataLoaded: false,
      width: 80
    }
  },
  components: {
    BeautyList,
    BannerList
  },
  computed: {
    title () {
      if (this.isDataLoaded) {
        return this.data.recommends.title
      }
      return ''
    },
    recommends () {
      if (this.isDataLoaded) {
        return this.data.recommends.recommends
      }
      return []
    },
    feeds () {
      if (this.isDataLoaded) {
        return this.data.feeds
      }
      return []
    }
  },
  async mounted () {
    const result = await getFollowData()
    this.data = result.data[0]
    this.isDataLoaded = JSON.stringify(this.data) !== '{}'
    console.info('follow mounted', result)
  }
}
</script>
<style>
</style>
<style scoped lang="scss">
  .follow {
    width: 100%;
    .recommend {
      text-align: center;
      .title {
        height: 40px;
        line-height: 40px;
        font-size: 14px;
        width: 100%;
      }
    }
  }
</style>



