<template>
  <div class="product-instruction">
    <div class="key">
      <div v-for="(tag, index) in tags" :key="index" class="tag">{{tag}}</div>
    </div>
    <div class="value">
      <div class="brand">: {{item.brand}}</div>
      <div>: {{item.name}}</div>
      <div>: {{item.location}}</div>
      <div>: {{item.scheme}}</div>
      <div>: ￥{{item.market_price}}</div>
      <div>: ￥{{item.price}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      title: '产品说明书',
      tags: ['品牌', '名称', '发源地', '规格', '市场价', '参考价']
    }
  },
  props: {
    item: {
      type: Object,
      default () {
        return {}
      }
    }
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    }
  }
}
</script>

<style scoped lang="scss">
  .product-instruction {
    margin-top: 20px;
    margin-left: 10px;
    width: 100%;
    display: flex;
    .key {
      display: flex;
      flex-flow: column;
      text-align-last: justify;
      letter-spacing: 5px;
      .tag {
        line-height: 20px;
      }
    }
    .value {
      div {
        line-height: 20px;
        color: gray;
      }
      .brand {
        color: #FF80AB;
      }
    }
  }
</style>



