<template>
  <div class="nail-list">
    <div class="list">
      <div class="left">
        <div class="nail" v-for="left in leftNails" :key="left.slug">
          <img :src="'http://meilapp.qiniucdn.com'+left.img" alt="">
          <div class="like">like {{left.like_count}}</div>
        </div>
      </div>
      <div class="right">
        <div class="nail" v-for="right in rightNails" :key="right.slug">
          <img :src="'http://meilapp.qiniucdn.com'+right.img" alt="">
          <div class="like">like {{right.like_count}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    leftNails: {
      type: Array,
      default () {
        return []
      }
    },
    rightNails: {
      type: Array,
      default () {
        return []
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .list {
    margin-top: 5px;
    border-top: 10px solid #EEE;
    padding-left: 1%;
    padding-right: 1%;
    display: flex;
    flex-flow: row nowrap;
    .left, .right {
      width: 50%;
      box-sizing: border-box;
      text-align: center;
      img {
        width: 97%;
      }
      .like {
        text-align: left;
        width: 100%;
        height: 30px;
        line-height: 30px;
        padding-left: 10px;
        font-size: 12px;
        word-spacing: 5px;
        color: gray;
      }
    }
  }
</style>



