<template>
  <div class="beauty-trail-home">
    <header-bar :title="title">
      <div slot="left-actions" @click="toBack()">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
    </header-bar>
    <div class="tabs">
      <div class="going tab" @click="tabs(0)" :class="{'selected': tab === 0}">进行中</div>
      <div class="toGo tab" @click="tabs(1)" :class="{'selected': tab === 1}">预热中</div>
      <div class="gone tab" @click="tabs(2)" :class="{'selected': tab === 2}">已结束</div>
    </div>
    <div class="view" v-if="tries.length > 0">
      <beauty-gone :goneItems="toTries" v-if="tab === 0"></beauty-gone>
      <beauty-gone :goneItems="goingTries" v-if="tab === 1"></beauty-gone>
      <beauty-gone :goneItems="goneTries" v-if="tab === 2"></beauty-gone>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../../components/HeaderBar.vue'
import BeautyGone from './BeautyGone.vue'
import { getFreeTryData } from '../../apis/request/getData.js'

export default {
  data () {
    return {
      title: '免费试用',
      tries: {},
      tab: 0
    }
  },
  components: {
    HeaderBar,
    BeautyGone
  },
  computed: {
    toTries () {
      if (this.tries.length > 0) {
        return this.tries.filter((item) => {
          return item.is_finish !== true
        })
      }
    },
    goingTries () {
      return []
    },
    goneTries () {
      if (this.tries.length > 0) {
        return this.tries.filter((item) => {
          return item.is_finish === true
        })
      }
    }
  },
  async mounted () {
    console.info('beauty-trail-home mounted')
    const goneData = await getFreeTryData()
    this.tries = goneData.data
    console.info(this.tries[0], 'mish tries')
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    },
    tabs (item) {
      this.tab = item
    }
  }
}
</script>

<style scoped lang="scss">
  .beauty-trail-home {
    width: 100%;
    height: 100%;
    .selected {
      color: pink;
    }
    .tabs {
      position: absolute;
      top: 64px;
      width: 100%;
      height: 44px;
      border-bottom: 0.5px solid #DDDDDD;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-flow: row nowrap;
      .tab {
        width: 25%;
        text-align: center;
      }
    }
    .view {
      position: absolute;
      top: 108px;
      width: 100%;
      height: calc(100% - 108px);
      overflow: auto;
    }
  }
</style>



