<template>
  <div class="product-detail">
    <header-bar :title="title">
      <div slot="left-actions" @click="toBack()" v-if="!isShowInstruction">
        <i class="fa fa-angle-left" aria-hidden="true"></i>
      </div>
      <div slot="left-actions" @click="hideInstruction()" v-else>x</div>
    </header-bar>
    <div class="view">
      <div class="product" v-if="hasData">
        <img class="banner" :src="'http://meilapp.qiniucdn.com'+basic.banner" alt="">
        <div class="text">
          <div class="short-name">{{basic.short_name}}</div>
          <div class="price">参考价 ￥{{attr.price}} | {{attr.scheme}}</div>
        </div>
      </div>
      <div class="instruction" @click="showInstruction()">产品说明书 ></div>
      <div class="detail">
        <div class="detail-tags">
          <div :class="{'selected': tag === 0}" @click="toTag(0)">口碑点评({{comment.rank}})</div>
          <div :class="{'selected': tag === 1}" @click="toTag(1)">相关话题({{vtalks.length}})</div>
          <div :class="{'selected': tag === 2}" @click="toTag(2)">相似产品({{similarProducts.length}})</div>
        </div>
        <div class="detail-items">
          <div class="comment-item" v-if="tag === 0">
            <div class="top">
              <div class="left">
                <div class="star">{{ext.star}}</div>
                <div class="commented"><span class="commented-count">{{ext.commented_count}}人 </span>点评</div>
              </div>
              <div class="right">
                <percentage :stars="statistics.star_types"></percentage>
              </div>
            </div>
            <div class="skin-age-text" @click="showAgeOrSkin()">
              <div class="title">肤质: </div>
              <percentage :stars="skins" :showPercent="false" :padding="true"></percentage>
              <div class="title">年龄: </div>
              <percentage :stars="ages" :showPercent="false"></percentage>
            </div>
            <div class="skin-age" v-show="isShowAgeOrSkin">
              <div class="skin">
                <div class="title">肤质比例</div>
                <percentage :stars="statistics.skin_types"></percentage>
              </div>
              <div class="age">
                <div class="title">年龄结构</div>
                <percentage :stars="statistics.get_age_ranges" :width="width"></percentage>
              </div>
            </div>
            <div class="comments">
              <comment-list
                color="black"
                :items="extraComments"
                :types="statistics.skin_types"
                :left='left'
                :top='top'
                :border="true"></comment-list>
            </div>
          </div>
          <div class="vtalks" v-if="tag === 1">
            <makeup-list :items="vtalks"></makeup-list>
          </div>
          <div class="similar-products" v-if="tag === 2">
            <product-list :items="similarProducts" :width="bannerWidth"></product-list>
          </div>
        </div>
      </div>
    </div>
    <div class="instruction-detail" v-if="isShowInstruction">
      <product-instruction :item="attr"></product-instruction>
    </div>
  </div>
</template>

<script>
import HeaderBar from '../components/HeaderBar.vue'
import Percentage from './Percentage.vue'
import CommentList from '../components/CommentList.vue'
import MakeupList from '../beauty/MakeupList.vue'
import ProductList from './ProductList.vue'
import ProductInstruction from './ProductInstruction.vue'
import { getProductDetail } from '../apis/request/getData.js'

export default {
  data () {
    return {
      title: '产品详情',
      data: {},
      hasData: false,
      tag: 0,
      isShowAgeOrSkin: false,
      left: 0,
      top: 10,
      width: 48,
      bannerWidth: 140,
      isShowInstruction: false
    }
  },
  computed: {
    skins () {
      if (this.hasData) {
        return this.statistics.skin_types.filter((item, index) => {
          return index === 0 || index === 2
        })
      }
    },
    ages () {
      if (this.hasData) {
        return this.statistics.get_age_ranges.filter((item, index) => {
          return index === 2
        })
      }
    },
    id () {
      return this.$route.query.id
    },
    basic () {
      if (this.hasData) {
        return this.data.basic
      }
      return {}
    },
    attr () {
      if (this.hasData) {
        return this.data.attr
      }
      return {}
    },
    comment () {
      if (this.hasData) {
        return this.data.comment
      }
      return {}
    },
    extraComments () {
      if (this.hasData) {
        return this.data.extra_comments
      }
      return []
    },
    vtalks () {
      if (this.hasData) {
        return this.data.vtalks
      }
      return []
    },
    similarProducts () {
      if (this.hasData) {
        return this.data.similar_products
      }
      return []
    },
    ext () {
      if (this.hasData) {
        return this.data.ext
      }
      return {}
    },
    statistics () {
      if (this.hasData) {
        return this.data.statistics
      }
      return {}
    }
  },
  async mounted () {
    const result = await getProductDetail(this.id)
    this.data = result.data[0]
    console.info('this.data', this.data)
    this.hasData = JSON.stringify(this.data) !== '{}'
  },
  components: {
    HeaderBar,
    Percentage,
    CommentList,
    MakeupList,
    ProductList,
    ProductInstruction
  },
  methods: {
    toBack () {
      this.$router.go(-1)
    },
    showAgeOrSkin () {
      if (this.isShowAgeOrSkin) {
        this.isShowAgeOrSkin = false
      } else {
        this.isShowAgeOrSkin = true
      }
    },
    toTag (index) {
      this.tag = index
    },
    showInstruction () {
      this.isShowInstruction = true
      this.title = '产品说明书'
    },
    hideInstruction () {
      this.isShowInstruction = false
      this.title = '产品详情'
    }
  }
}
</script>

<style scoped lang="scss">
  .product-detail {
    width: 100%;
    // position: relative;
    .selected {
      color: #FF80AB;
      border-bottom: 1px solid #FF80AB;
    }
    .view {
      position: absolute;
      top: 64px;
      height: calc(100% - 64px);
      overflow: auto;
      width: 100%;
      overflow-x: hidden;
      .product {
        display: flex;
        flex-flow: row nowrap;
        padding: 5px 0;
        border-bottom: 0.5px solid #ddd;
        .banner {
          width: 110px;
          height: 110px;
          margin-left: 10px;
        }
        .text {
          margin-left: 40px;
          padding-top: 15px;
          font-size: 13px;
          .price {
            color: darkgrey;
            line-height: 50px;
            font-size: 12px;
          }
        }
      }
      .instruction {
        height: 45px;
        line-height: 45px;
        color: #FF80AB;
        font-size: 14px;
        text-align: center;
        border-bottom: 10px solid #eee;
      }
      .detail {
        .detail-tags {
          display: flex;
          flex-flow: row nowrap;
          width: 100%;
          height: 35px;
          justify-content: space-between;
          padding: 0px 25px;
          box-sizing: border-box;
          line-height: 35px;
          border-bottom: 1px solid #ddd;
          font-size: 13px;
        }
        .detail-items {
          .comment-item {
            .top {
              position: relative;
              display: flex;
              flex-flow: row nowrap;
              align-items: center;
              margin-top: 20px;
              .left, .right {
                box-sizing: border-box;
              }
              .left {
                width: 40%;
                text-align: center;
                .star {
                  font-size: 30px;
                  font-weight: 400;
                }
                .commented {
                  font-size: 13px;
                  .commented-count {
                    color: #FF80AB;
                  }
                }
              }
              .right {
                padding-left: 10%;
                width: 60%;
                border-left: 1px solid #ddd;
              }
            }
            .skin-age-text {
              display: flex;
              font-size: 13px;
              color: gray;
              width: 85%;
              background-color: #ddd;
              margin: 0 auto;
              justify-content: space-between;
              padding: 0 15px;
              border-radius: 20px;
              line-height: 30px;
              height: 30px;
              margin-top: 10px;
              margin-bottom: 10px;
              .title {
                color: black;
              }
              .percentage {
                display: flex;
              }
            }
            .skin-age {
              display: flex;
              flex-flow: row nowrap;
              justify-content: space-around;
              padding-bottom: 12px;
              border-bottom: 0.5px solid #ddd;
              .title {
                text-align: center;
                padding: 10px 0 20px 0;
                font-size: 15px;
              }
            }
          }
          .vtalks {
            margin-top: 10px;
          }
        }
      }
    }
    .instruction-detail {
      position: absolute;
      top: 64px;
      padding-left: 10px;
      box-sizing: border-box;
      width: 100%;
      height: calc(100% - 64px);
      background-color: white;
      font-size: 12px;
    }
  }
</style>



